'use client';
import React, { useState } from 'react';


const users = [
  { id: 1, name: 'John Doe', email: 'john@example.com', role: 'admin', subscription: 'A', avatar: '/images/user.jpg' },
  { id: 2, name: 'Jane Smith', email: 'jane@example.com', role: 'user', subscription: 'B', avatar: '/images/user.jpg' },
  { id: 3, name: 'Alice Johnson', email: 'alice@example.com', role: 'user', subscription: 'C', avatar: '/images/user.jpg' },
];

const roles = ['All', 'admin', 'user'];
const subscriptions = ['All', 'A', 'B', 'C'];

export default function AdminDashboard() {
  const [selectedUser, setSelectedUser] = useState(null);
  const [roleFilter, setRoleFilter] = useState('All');
  const [subscriptionFilter, setSubscriptionFilter] = useState('All');

  const filteredUsers = users.filter(user => {
    return (
      (roleFilter === 'All' || user.role === roleFilter) &&
      (subscriptionFilter === 'All' || user.subscription === subscriptionFilter)
    );
  });

  const handleChange = (field, value) => {
    setSelectedUser(prev => ({ ...prev, [field]: value }));
  };

  return (
    <div className="flex h-screen overflow-hidden">
      <div className="w-1/4 bg-amber-50 p-4 space-y-4">
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-1">Роль</label>
          <Select value={roleFilter} onValueChange={setRoleFilter}>
            <SelectTrigger>
              <SelectValue />
            </SelectTrigger>
            <SelectContent>
              {roles.map(role => (
                <SelectItem key={role} value={role}>{role}</SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>

        <div>
          <label className="block text-sm font-medium text-gray-700 mb-1">Подписка</label>
          <Select value={subscriptionFilter} onValueChange={setSubscriptionFilter}>
            <SelectTrigger>
              <SelectValue />
            </SelectTrigger>
            <SelectContent>
              {subscriptions.map(sub => (
                <SelectItem key={sub} value={sub}>{sub}</SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>

        <ScrollArea className="h-[calc(100vh-12rem)] pr-2">
          {filteredUsers.map(user => (
            <div
              key={user.id}
              className={`flex items-center justify-between px-3 py-2 rounded-lg cursor-pointer mb-2 ${selectedUser?.id === user.id ? 'border border-cyan-500' : 'hover:bg-slate-100'}`}
              onClick={() => setSelectedUser(user)}
            >
              <div className="flex items-center space-x-2">
                <Avatar className="h-6 w-6">
                  <AvatarImage src={user.avatar || "/images/user.jpg"} />
                  <AvatarFallback>{user.name[0]}</AvatarFallback>
                </Avatar>
                <span>{user.name}</span>
              </div>
              <span className="inline-flex items-center justify-center w-5 h-5 text-xs bg-white border border-gray-300 rounded">{user.subscription}</span>
            </div>
          ))}
        </ScrollArea>
      </div>

      <div className="w-3/4 p-8 bg-gray-100 overflow-auto">
        {selectedUser ? (
          <Card className="shadow-xl">
            <CardContent className="p-6">
              <h2 className="text-2xl font-semibold mb-4">Информация о пользователе</h2>
              <p><strong>Имя:</strong> <Input value={selectedUser.name} onChange={(e) => handleChange('name', e.target.value)} className="inline w-auto ml-2" /></p>
              <p><strong>Email:</strong> <Input value={selectedUser.email} onChange={(e) => handleChange('email', e.target.value)} className="inline w-auto ml-2" /></p>
              <p><strong>Роль:</strong> <Input value={selectedUser.role} onChange={(e) => handleChange('role', e.target.value)} className="inline w-auto ml-2" /></p>
              <p><strong>Подписка:</strong> <Input value={selectedUser.subscription} onChange={(e) => handleChange('subscription', e.target.value)} className="inline w-auto ml-2" /></p>
            </CardContent>
          </Card>
        ) : (
          <p className="text-gray-500">Выберите пользователя, чтобы посмотреть информацию</p>
        )}
      </div>
    </div>
  );
}
